import React from "react";
import Layout from "../components/layout";
import Intro from "../components/intro";
import Tabs from "../components/tabs";
import { graphql } from "gatsby"
import Button from "../components/button";
import DB from "../components/db";

const deleteAll = () => {
  DB.table('members').clear();
  DB.table('meetings').clear();
};

const DeleteAll = props => (
  <div className="level" style={{marginBottom: `10px`}}>
    <div className="level-item">
      <Button onClick= {deleteAll} class="fas fa-lg fa-trash-alt has-text-danger" text="Delete"/>
    </div>
  </div>
)


export default ({data}) => {
  return(
    <Layout>
      <Tabs />
      <Intro data={data}/>
      <DeleteAll />
    </Layout>
  );
}

export const query = graphql`
  query {
    hive: file(relativePath: { eq: "hive.jpg" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 64, height: 64) {
          ...GatsbyImageSharpFixed
        }
      }
    }

    care: file(relativePath: { eq: "care.png" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 64, height: 64) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`
